import Vue from 'vue'
import axios from 'axios'
axios.defaults.widthCredentials = true;
import qs from 'qs';
//Vue.prototype.$qs = qs;
//http://192.168.0.120:8090/
//http://localhost:8085/
//http://h5.pingnuosoft.com/
Vue.prototype.$axios = axios
axios.defaults.baseURL = 'http://h5.pingnuosoft.com/'
Vue.prototype.$api = axios.defaults.baseURL
Vue.prototype.$link = axios.defaults.baseURL+'MobileAnim/index.html'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// http request 请求拦截器
axios.interceptors.request.use(
	function(config) {
//		console.log(config.data)
		if(config.method == "post") {
			config.data = qs.stringify(config.data);
//			console.log(config.data)
		}
		return config;
	},
	function(error) {
		return Promise.reject(error)
	}
);

// http response 服务器响应拦截器，
axios.interceptors.response.use(
	function(response) {
//		console.log(response.data)
		if(typeof(response.data) == "string") {

		}
		return response;
	},
	function(error) {
		return Promise.reject(error);
	}
)
