// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
require("babel-polyfill");
require("es6-promise").polyfill();
require("es6-promise/auto");
import Vue from 'vue'
import App from './App'
import router from './router'

import ElementUI from 'element-ui' //element-ui的全部组件
import 'element-ui/lib/theme-chalk/index.css' //element-ui的css
import '@/assets/style/font-awesome.min.css' //字体库
import '@/assets/style/reset.css'
import '@/assets/style/font-huakang.scss'
import '@/assets/style/animate.min.css' //动画库
//import VueAwesomeSwiper from 'vue-awesome-swiper'
import utill from '@/assets/script/utill.js'
Vue.prototype.utill = utill
import http from '@/assets/script/axios/http.js'
Vue.prototype.http = http
import 'swiper/dist/css/swiper.css'
import axios from 'axios'
import VueCropper from 'vue-cropperjs';
Vue.component(VueCropper);

import VueDND from 'awe-dnd'
Vue.use(VueDND)

Vue.use(ElementUI)
Vue.config.productionTip = false;
import vuex from 'vuex'
Vue.use(vuex);

const getters={
  getWiFiList: state => {
    return state.ElementData;
  }
};
var store = new vuex.Store({ //store对象
	state: {
		user:{
			username:undefined,
			name:'',
			department:'',
			create_time:'',
			phone:'',
			grade:'1',
			headSrc: 'Uploads/default/default_head.jpg',
		},
		isShowLogin:false,//是否显示登录框
		isShowRegister:false,//是否显示注册框
		editSubnavNum:0,//右侧编辑栏切换
		
		isShowPreview: false, //显示预览框
		//右侧编辑栏显示编号
		editNavNum: 2,		
		//聚焦页面的ID
		focusPage: '', //聚焦的页面
		focusPart:[], //聚焦的组件
		focusPart1:[],
		collapse:1,//当前样式折叠面板
		isCtrlDown:false,// ctrl点击
		dragging:{},//存储排序的数据
		scaleRatio:100,//缩放比例
		
		//右侧编辑器的数据
		InitEditData: '',
		//存储组件所有数据
		CoverImg: 'Uploads/default/cover.png',
		Swiper: { //存储swiper状态
			direction: 'vertical',
			type: 'slide',
			loop: 'false',
			autoplay :5,
			isAutoPlay:false,
		},
		Title: '未命名场景',
		Describe: '',
		ElementData: [],//存储最新组件数据		
		oldElementData:[],//存储旧的数据	
		oldCacheElementData:[],//上一次的缓存数据
		cacheElementData:[],//缓存数据
		startElementData:[],//初始数据
		
		startElementInfo:[],//存储初始场景信息
		oldElementInfo:[], //存储旧的场景信息
		
		ElementClipboard:[], //组件粘贴板
		 
		Music: '',
		animStore: {
			id: '',
			className: ''
		},
		Form:[],
		Svgs:[],
	},
	getters
});

/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	components: {
		App
	},
	template: '<App/>'
})


