<template>
	<div class="info-page">
		<div class="info box-list">
			<h2 class="info-title">个人资料</h2>
			<div class="now-info">
				<el-row :gutter="100">
					<el-col :span="12">
						<div class="info-head">
							<el-upload :auto-upload="false" :action="$api+'/index.php/Home/Material/UploadBlob'" class="head-uploader"  :on-change="beforeHeadUpload">
								<img  style="width: 120px;height: 120px;" :src="$api+$store.state.user.headSrc"/>
							</el-upload>
						</div>
						<div class="info-detail-l">
							<li class="info-username">{{$store.state.user.username}}</li>
							<li class="info-name">姓名：{{$store.state.user.name}}</li>
							<li class="info-type" v-if='$store.state.user.grade == 1'>账号类型：普通会员</li>
							<li class="info-type" v-if='$store.state.user.grade == 2'>账号类型：管理员</li>
							<li class="info-time">创建时间：{{utill.timestampToTime($store.state.user.create_time)}}</li>
						</div>
					</el-col>
					<el-col :span="12">
						<div class="info-detail-r">
							<li></li>
							<li class="info-depart">部门：{{$store.state.user.department}}</li>
							<li class="info-time">手机号：{{$store.state.user.phone?$store.state.user.phone.substr(0, 3) + '****' + $store.state.user.phone.substr(7):''}}</li>
						</div>
					</el-col>
				</el-row>
				<el-button class='btn-edit' type="primary" size='mini' icon="el-icon-edit" @click="updateInfo">修改资料</el-button>
			</div>
			<el-dialog title="修改资料  " :close-on-click-modal='false' :visible.sync="dialogInfo" width='500px' @close="closeDialogInfo('infoForm')">
				<el-form :model="infoForm" style='margin: 0 20px;' ref='infoForm' :rules='infoRule'>
					<el-form-item label="姓名" prop='name' label-width="80px">
						<el-input v-model.trim="infoForm.name" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="部门" prop='department' label-width="80px">
						<el-input v-model.trim="infoForm.department" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item label="手机号" prop='phone' label-width="80px">
						<el-input v-model.trim="infoForm.phone" autocomplete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="dialogInfo = false">取 消</el-button>
					<el-button type="primary" @click="saveInfoForm('infoForm')">保存</el-button>
				</div>
			</el-dialog>
			<el-dialog title="头像裁剪" :visible.sync="dialogBlob" v-if='dialogBlob' width='960px' :close-on-click-modal='false'>
				<blob :src='headBlobSrc' type="head" ref='headBlob' @showBack = "showBack"></blob>
				<div style="text-align: right;margin-top: 20px;">
					<el-button type="primary" @click="setBlob">确定</el-button>
					<el-button plain @click='dialogBlob = false'>取消</el-button>
				</div>
			</el-dialog>
		</div>
		<div class="safe box-list">
			<h2 class="info-title">修改密码</h2>
			<el-form :model="userPassForm" style='margin: 20px;width:400px' ref='userPassForm' :rules='userPassFormRule'>
				<el-form-item label="用户名：" prop='username' label-width="120px">
					<el-input v-model.trim="$store.state.user.username" autocomplete="off" disabled></el-input>
				</el-form-item>
				<el-form-item label="原密码：" prop='oldPassword' label-width="120px">
					<el-input type='password' v-model.trim="userPassForm.oldPassword" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="新的密码：" prop='newPassword' label-width="120px">
					<el-input type='password' v-model.trim="userPassForm.newPassword" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认密码：" prop='checkNewPassword' label-width="120px">
					<el-input type='password' v-model.trim="userPassForm.checkNewPassword" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label-width="120px">
					<el-button style='width: 100%;' type="primary" @click="updateUserPass('userPassForm')">修改密码</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
	import Blob from '@/components/Blob'
	export default {
		components: {
			blob: Blob
		},
		data() {
			var checkPhone = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入手机号'));
				} else if(!(/^1[34578]\d{9}$/.test(value))) {
					callback(new Error('输入的手机号码有误'));
				} else {
					callback();
				}
			};
			var validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请输入新密码'));
				} else {
					if(value == this.userPassForm.oldPassword) {
						callback(new Error('新设置的密码和旧密码相同'));
					}
					if(this.userPassForm.checkNewPassword !== '') {
						this.$refs.userPassForm.validateField('checkNewPassword');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请再次输入密码'));
				} else if(value !== this.userPassForm.newPassword) {
					callback(new Error('两次输入密码不一致'));
				} else {
					callback();
				}
			};
			return {
				oldInfoData: { //存储旧的用户资料数据
					name: '',
					department: '',
					phone: ''
				},
				headBlobSrc: '',//待裁剪图片地址
				headSrc:'',//头像地址
				dialogInfo: false, //修改资料弹窗
				dialogBlob: false, //图片裁剪弹窗
				infoForm: {
					name: '',
					department: '',
					phone: ''
				},
				infoRule: {
					phone: [{
						required: true,
						validator: checkPhone,
						trigger: 'blur'
					}],
					name: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}],
					department: [{
						required: true,
						message: '请输入部门',
						trigger: 'blur'
					}],
				},
				//修改密码表单
				userPassForm: {
					oldPassword: '',
					newPassword: '',
					checkNewPassword: ''
				},
				userPassFormRule: {
					oldPassword: [{
						required: true,
						message: '请输入原密码',
						trigger: 'blur'
					}],
					newPassword: [{
						required: true,
						validator: validatePass,
						trigger: 'blur'
					}],
					checkNewPassword: [{
						required: true,
						validator: validatePass2,
						trigger: 'blur'
					}],

				}
			}
		},
		methods: {
			//上传图片前
			beforeHeadUpload(file) {
				const isJPG = file.raw.type === 'image/jpeg';
				const isPNG = file.raw.type === 'image/png';
				const isLt2M = file.raw.size / 1024 / 1024 < 2;
				if(!isJPG && !isPNG) {
					this.$message.error('上传头像图片只能是 JPG和PNG格式!');
					return false;
				}
				if(!isLt2M) {
					this.$message.error('上传头像图片大小不能超过 2MB!');
					return false;
				}
				this.readsImage(file.raw)
			},
			//转换图片为base64
			readsImage(fil){
				var Vue = this;
				var reader=new FileReader()
				reader.readAsDataURL(fil)
				reader.onload=function(){
					Vue.headBlobSrc =  reader.result
					Vue.dialogBlob = true;
				}
			},
			//调用裁剪
			setBlob() {
				this.$refs.headBlob.uploadBlob()
			},
			//显示头像
			showBack(src){
				this.axiosUpdateUserHead(src)
			},
			//初始化旧用户资料数据
			initoldInfoData() {
				this.oldInfoData.name = this.$store.state.user.name
				this.oldInfoData.department = this.$store.state.user.department
				this.oldInfoData.phone = this.$store.state.user.phone
			},
			//初始化用户资料表单
			initInfoForm() {
				this.infoForm.name = this.$store.state.user.name
				this.infoForm.department = this.$store.state.user.department
				this.infoForm.phone = this.$store.state.user.phone
			},
			//保存用资料表单
			saveInfoForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.axiosUpdateUserInfo()
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//修改密码
			updateUserPass(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.axiosUpdateUserPass()
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//修改资料
			updateInfo() {
				this.initInfoForm();
				this.initoldInfoData();
				this.dialogInfo = true
			},
			//关闭修改资料弹窗
			closeDialogInfo(formName) {
				this.dialogInfo = false;
			},
			//修改用户资料
			axiosUpdateUserInfo() {
				if(this.oldInfoData.name == this.infoForm.name &&
					this.oldInfoData.department == this.infoForm.department &&
					this.oldInfoData.phone == this.infoForm.phone) {
					this.$message.error('数据无变化');
					return false
				}
				this.openLoading()
				this.http.axiosUpdateUserInfo({
					username: this.$store.state.user.username,
					grade: this.$store.state.user.grade,
					name: this.infoForm.name,
					department: this.infoForm.department,
					phone: this.infoForm.phone
				}).then((res) => {
					if(res.data.status == 1) {
						this.$message.success('修改成功');
						this.$store.state.user.name = this.infoForm.name;
						this.$store.state.user.department = this.infoForm.department;
						this.$store.state.user.phone = this.infoForm.phone;
						this.dialogInfo = false;
						this.initoldInfoData();
					} else {
						this.$message.error('修改失败');
					}
					this.closeLoading()
				})
			},
			//修改密码接口
			axiosUpdateUserPass() {
				this.openLoading()
				this.http.axiosUpdateUserPass({
					username: this.$store.state.user.username,
					grade: this.$store.state.user.grade,
					oldPassword: this.utill.getAES(this.userPassForm.oldPassword),
					newPassword: this.utill.getAES(this.userPassForm.newPassword),
				}).then((res) => {
					
					if(res.data.status == 1) {
						this.$message.success(res.data.msg)
					} else {
						this.$message.error(res.data.msg);
					}
					this.closeLoading()
				})
			},
			//修改头像
			axiosUpdateUserHead(src) {
				this.openLoading()
				this.http.axiosUpdateUserHead({
					username: this.$store.state.user.username,
					grade: this.$store.state.user.grade,
					head_src:src 
				}).then((res) => {
					this.dialogBlob = false
					if(res.data.status == 1) {
						this.$store.state.user.headSrc = src
						this.$message.success(res.data.msg)
					} else {
						this.$message.error(res.data.msg);
					}
					this.closeLoading()
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},

		}
	}
</script>


